<template>
  <div id="restartLnb">
    <ul>
      <li v-for="child in activeRouteChildren" :key="child.path">
        <router-link :to="`${activeRoute.path}/${child.path}`">
          {{ child.name }}
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    activeRoute: {
      type: Object,
      default: () => null
    }
  },
  computed: {
    activeRouteChildren() {
      return this.activeRoute && this.activeRoute.children ? this.activeRoute.children : [];
    }
  }
};
</script>
