<template>
  <div v-if="showRestartAbout" id="restartAbout">
    <h2>ABOUT</h2>		
  </div>
  <router-view />
</template>

<script>
export default {
  name: 'restartAbout',
  computed: {
    showRestartAbout() {      
      return this.$route.path === '/about';
    }
  }
};
</script>
