<template>
  <div id="restartNav">
    <nav>
      <ul>
        <li v-for="route in routes" :key="route.path"
          @click="setActiveRoute(route)">
            <router-link :to="route.path">
              {{ route.name }}
            </router-link>
        </li>
      </ul>
    </nav>  
  </div>
</template>

<script>
import { routes } from '../router/index.js'; // Adjust the path as necessary

export default {
  data() {
    return {
      routes
    };
  },
  methods: {
    setActiveRoute(route) {
      this.$emit('routeChange', route);      
    },  
  }
};
</script>

