import { createRouter, createWebHistory } from 'vue-router'

import restartHome from '../components/restartHome'
import restartShop from '../components/restartShop'
import restartContact from '../components/restartContact'
import restartAbout from '../components/restartAbout'
import homeSub1 from '../components/homeSub1'
import homeSub2 from '../components/homeSub2'
import shopSub1 from '../components/shopSub1'
import contactSub1 from '../components/contactSub1'
import aboutSub1 from '../components/aboutSub1'

// routes.js
export const routes = [
  { path: '/home',
    component: restartHome,
    name: 'Home',
    children: [
      { path: 'homeSub1',
        component: homeSub1,
        name: 'homeSub1' 
      },
      { path: 'homeSub2',
        component: homeSub2,
        name: 'homeSub2' 
      }
    ]
  },
  { path: '/shop',
    component: restartShop,
    name: 'Shop',
    children: [
      { path: 'shopSub1',
        component: shopSub1,
        name: 'shopSub1'
      }
    ]
  },
  { path: '/contact',
    component: restartContact,
    name: 'Contact',
    children: [
      { path: 'contactSub1',
        component: contactSub1,
        name: 'contactSub1' 
      }
    ]
  },
  { path: '/about',
    component: restartAbout,
    name: 'About',
    children: [
      { path: 'aboutSub1',
        component: aboutSub1,
        name: 'aboutSub1'
      }
    ]
  }
];


const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
});

export {router}
